import { bigThan } from "@/request/stock";
import moment from "moment";
export default {
  name: 'PerfectList',
  data: function data() {
    return {
      ppx: 0,
      rightX: 3,
      num: 0,
      stockListStr: '',
      stockList: [],
      type: "CCI",
      date: new Date()
    };
  },
  created: function created() {},
  methods: {
    mouthMove: function mouthMove(event) {
      console.log("mouth move", event.offsetX);
      this.ppx = event.offsetX;
    },
    stockListChange: function stockListChange() {
      console.log(this.stockListStr.split("\n"));
      this.stockList.length = 0;
      this.stockList = this.stockListStr.split("\n");
    },
    cc: function cc() {
      var _this = this;

      bigThan({
        date: moment(this.date).format("YYYY-MM-DD")
      }).then(function (resp) {
        _this.rightX = 3 + parseInt(resp.data) * 4.23;
      });
    },
    getStockNum: function getStockNum(stockNum) {
      console.log(stockNum);

      if (stockNum.startsWith("6")) {
        return "1." + stockNum;
      } else if (stockNum.startsWith("3")) {
        return "0." + stockNum;
      } else if (stockNum.startsWith("0")) {
        return "0." + stockNum;
      } else {
        return "0." + stockNum;
      }
    },
    changeImg: function changeImg(stock, type) {
      this.type = type;
    }
  }
};